@import url('https://fonts.googleapis.com/css2?family=Anton&family=Bebas+Neue&family=Merriweather+Sans:wght@300&display=swap');

body{
  height:100vh;
  background-color:purple;
  
}



#quoteContainer{
 background-color:white;
 padding:3%;
 margin-bottom:10px;
 margin-top:10px;
 box-shadow: 3px 3px gray;
 
 
  
}

#author-container{
  margin-top:0px;
}
#text{
  margin-bottom:5px;
  font-family: 'Merriweather Sans', sans-serif;
}

#author{
  font-family: 'Merriweather Sans', sans-serif;
}

i{
  padding:0px;
}


.wrapper{
  height:100vh;
  background-color:lightblue;
  
}

.blue{
  color:blue;
}


.buttons{
 height:38px;
   width:25vh;
}
#quote-text{
  margin-bottom:10px;
}


#new-quote{
  line-height:20px;
}
#tweet-quote{
  border-style:solid;
  
  border-radius:5px;
  font-size:30px;
  width:40px;
}
.t-icon{
  width:100%;
  color:white;
  
  text-align:center;
  
}

#dislike{
  color:black;
  text-decoration:none;
  border:none;
  background:none;
}

#like{
  border:none;
  background:none;
  padding-right:10px;
  color:black;
  text-decoration:none;
}

#like:hover{
 color:gray;
}
#dislike:hover{
  color:gray
}

@media (max-width:400px){
   #quoteContainer{
    width:80%;
    
  }

  .buttons-div{
    margin-bottom:10px;
  }
  #new-quote{
    height:30px;
    width:100px;
    font-size:14px;
  }
  
  #tweet-quote{
    height:30px;
    width:30px;
    background-color:blue;
    font-size:18px;
  }
  
  #text{
    font-size:18px;
  }
  
  #author{
    font-size:18px;
  }

  #like{
    padding-left:0px;
    padding-right:3px;
  }
}


@media (min-width:400px){
  
 
  
  #new-quote{
    height:45px;
    width:160px;
  }
  #tweet-quote{
    height:45px;
    width:45px;
  }
  
  
  p{
    font-size:25px;
    line-height:45px;
  }
  
  #dislike{
   font-size:25px;
  }
  #like{
    font-size:25px;
    margin-right:15px;
  }
  
 
  
}


@media (min-width:600px){
  #new-quote{
    height:50px;
    width:175px;
    font-size:20px;
  }
 
  .t-icon{
    font-size:20px;
   
  }
  p{
    font-size:30px;
    line-height:55px;
  }
}
@media (max-height:250px){
 #quote-text{
   margin-bottom:3px;
 }
 #text{
   margin-bottom:0px;
 }
  p{
    font-size:20px;
  }
  #new-quote{
    height:30px;
    width:110px;
    font-size:14px;
  }
  
  #tweet-quote{
    height:30px;
    width:30px;
    background-color:purple;
    font-size:15px;
  }
  .button-div{
   padding-bottom: 10px;
  }
  
  #dislike{
    font-size:18px;
    padding-left:0px;
  }
  #like{
    font-size:18px;
    padding:0px;
  }
  
  
  
}


